import { Value, Block } from '@ali/4ever-cangjie';
export default function isSelectionInList(value: Value) {
  const { selection, document } = value;
  const paragraph = document
    // @ts-ignore
    .getClosest(selection.anchor.key, ({ type }) => type === 'paragraph') as Block;
  const list = paragraph.data.list;

  return list;
}
